import { setMode } from '@stencil/core';
import { loadUserPreference, getAppliedTheme, setAppliedTheme, getMode } from '../components/utils/theme';


export default () => {
  setMode(() => getMode());
};

// Ajuste inicial do tema
const userPreference = loadUserPreference();
setAppliedTheme(getAppliedTheme(userPreference));